import React from 'react';
import { connect } from 'react-redux'
import { Button, List } from 'antd';
import { Card } from 'antd';
import { isReadAction } from '../../../redux/actions/isreadAction'

//UI
function Message(props) {

  //
  const data = [
    'Racing car sprays burning fuel into crowd.',
  ];

  //
  const isRead = () => props.isRead(true)

  return (
    <div>
      <Card className='card_style' title="" extra={<Button type="primary" onClick={isRead}>  </Button>}>
        <List
          footer={<div> {data.length} </div>}
          bordered
          dataSource={data}
          renderItem={item => (
            <List.Item style={{ display: 'flex', justifyContent: 'space-between' }}>
              {item}
              {!props.result.read &&
                <Button size="small" onClick={isRead} ></Button>
              }
            </List.Item>
          )}
        />
      </Card>

    </div>
  );
}

//

//mapStateToProps
//keyUIpropskey,valueUIpropsvalue

function mapStateToProps(state) {
  return { result: state }
}

//mapDispatchToProps
//keyUIpropskey,valueUIpropsvalue

function mapDispatchToProps(dispatch) {
  return {
    isRead: () => dispatch(isReadAction())
  }
}

//connect()()Count
export default connect(mapStateToProps, mapDispatchToProps)(Message)

