import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom'
import { Input, Card, Form, Row, Checkbox, Col, Upload, Button, message, DatePicker, Space } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import moment from 'moment';
import { createApi, getOneById, modifyOne } from '../../../../services/activities'
import './Edit.less'

export default function Edit() {

  //url
  let params = new URLSearchParams(window.location.search);
  const editKey = params.get("id");

  //urlkey
  const [form] = Form.useForm();
  useEffect(() => {
    if (editKey) {
      getOneById(editKey).then(res => {
        console.log(res);
        form.setFieldsValue({ activityName: res.activityName, type: res.type, activityDate: moment(res.activityDate) });
      })
    }
  }, []);

  //
  function nameOnChange(event) {
    console.log('', event.target.value);
  }

  //
  function typeOnChange(checkedValues) {
    console.log('', checkedValues);
  }

  //
  function dateOnChange(date, dateString) {
    console.log('date', date);
    console.log('dateString', dateString);
  }

  //
  const props = {
    name: 'file',
    action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
    headers: {
      authorization: 'authorization-text',
    },
    onChange(info) {
      if (info.file.status !== 'uploading') {
        console.log(info.file.response.url);
      }
      if (info.file.status === 'done') {
        message.success(`${info.file.name} `);
      } else if (info.file.status === 'error') {
        message.error(`${info.file.name} `);
      }
    },
  };
  const normFile = (event) => {
    console.log('Upload event:', event.file.response);
    if (Array.isArray(event)) {
      return event;
    }
    return event && event.fileList;
  };

  const navigate = useNavigate();

   //
  const onFinish = (values) => {
    console.log('', values);
    if (values) {
      console.log('', values);
      if (editKey) {
        // modifyOne
        modifyOne(editKey, { ...values }).then(res => {
          console.log('', res)
          navigate("/admin/activities")
        })
          .catch(err => {
            console.log(err)
          })
      } else {
        // createApi
        createApi({ ...values, onlineState: false }).then(res => {
          console.log('', res)
          navigate('/admin/activities')
        }).catch(err => {
          console.log(err)
        })
      }
    }
  };

  //
  const onFinishFailed = (errorInfo) => {
    console.log('', errorInfo);
    message.error('')

  };

  return (
    <div className='container_style'>
      <Card className='card_style' title={editKey ? "" : ''}  >
        <div className='form_container_style'>
          <Form className='form_style'
            name="basic"
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            form={form}
            autoComplete="off"
          >
            <Form.Item
              name='activityName'
              label=""
              rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" onChange={nameOnChange} />
            </Form.Item>

            <Form.Item
              name='activityDate'
              label=""
              rules={[{ required: true, message: '' }]}
            >
              <DatePicker placeholder="" onChange={dateOnChange} />
            </Form.Item>

            <Form.Item name='type' label="" rules={[{ required: true, message: '' }]}>
              <Checkbox.Group onChange={typeOnChange}>
                <Row className='row_style'>
                  <Col span={5}><Checkbox value=""></Checkbox></Col>
                  <Col span={5}><Checkbox value=""></Checkbox></Col>
                  <Col span={5}><Checkbox value=""></Checkbox></Col>
                  <Col span={5}><Checkbox value=""></Checkbox></Col>
                  <Col span={5}><Checkbox value=""></Checkbox></Col>
                </Row>
              </Checkbox.Group>
            </Form.Item>

            <Form.Item
              label=""
              name="coverPic"
              action="http://localhost:5000/activities"
              valuePropName="fileList"
              // 
              getValueFromEvent={normFile}
            >
              <Upload {...props}>
                <Button icon={<UploadOutlined />}></Button>
              </Upload>
            </Form.Item>

            <Form.Item >
              <Space>
                <Button type="primary" htmlType="submit" ></Button>
                <Button htmlType="reset" ></Button>
                <Button onClick={ () => navigate("/admin/activities")}></Button>
              </Space>
            </Form.Item>

          </Form>
        </div>
      </Card>
    </div>
  );
}
