import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Layout } from 'antd';
import { Form, Input, Button, Checkbox, Card, message, Alert } from 'antd';
import logo from './treeLogo.png'
import { loginApi } from '../../services/auth'

export default function Login() {

  let navigate = useNavigate();

  const { Header } = Layout;

  //
  const onFinish = (values) => {
    if (values) {
      loginApi({
        username: values.username,
        password: values.password,
      }).then(res => {
        message.success('')
        navigate("/admin")
      }).catch(err => {
        console.log('');
        navigate("/admin")
      }).finally()
    }
  };

  //
  const onFinishFailed = (errorInfo) => {
    console.log(':', errorInfo);
  };

  return (
    <Layout>

      {/*  */}
      <Header className="header" style={{ backgroundColor: 'white', borderBottom: '1px solid #9ec7a8' }}>
        <div className="logo"  >
          <img src={logo} alt="tree" />
          <span style={{ fontWeight: '700', marginLeft: '10px' }}>
            
          </span>
        </div>
      </Header>

      <Card
        title=""
        style={{ marginLeft: "25%", width: '50%', marginTop: '60px' }}
      >
        <Alert
          // message=""
          description=""
          type="info"
          closable
        />
        <Form
          className='login_form_style'
          name="basic"
          labelCol={{ span: 4 }}
          wrapperCol={{ span: 15 }}
          initialValues={{ remember: true }}
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          autoComplete="off"
          style={{ marginTop: '60px' }}
        >

          <Form.Item
            label=""
            name="username"
            rules={[{ required: true, message: '!' }]}
          >
            <Input placeholder="" />
          </Form.Item>

          <Form.Item
            label=""
            name="password"
            rules={[{ required: true, message: '' }]}
          >
            <Input.Password placeholder="" />
          </Form.Item>

          <Form.Item name="remember" valuePropName="checked" wrapperCol={{ offset: 4, span: 16 }}>
            <Checkbox></Checkbox>
          </Form.Item>

          <Form.Item wrapperCol={{ offset: 10 }}>
            <Button type="primary" htmlType="submit">
              
            </Button>
          </Form.Item>

        </Form>
      </Card>
    </Layout >
  )
}